.form {
   position: relative;
   
}

.form__input {
   width: 300px;
   background-color: #fff;
   padding: 5px 20px;
   margin-top: 20px;
   border-radius: 5px;
}

.form__btn {
   position: absolute;
   right: 4px;
   bottom: 3px;

   background-color: #000;
   color: #fff;
   padding: 1px 15px;
   border-radius: 5px;
   transition: background-color 0.2s ease-in-out;
   border: 1px solid #000;
   font-weight: 500;
}

.form__btn:hover {
   background-color: #fff;
   color: #000;
   border: 1px solid #000;
}