.subs {
   background-color: #e5c643;
   margin-top: 40px;
}
.subs__body {
   padding: 80px 0;
   display: flex;
   flex-direction: column;
   justify-content: center;
   align-items: center;
}

.subs__title {
   color: #fff;
   font-size: 40px;
   font-weight: 900;
   line-height: 60px;
   letter-spacing: 1.65px;
   text-align: center;
}

.subs__text {
   color: #FFFCF8;
   font-size: 18px;
   font-weight: 400;
   line-height: 35px;
   letter-spacing: 0.96px;
   text-align: center;
}