.footer {
   background-color: #000;
   color: #fff;
}

a {
   color: inherit;
}

.footer__body {
   padding: 100px;
   display: flex;
   column-gap: 20px;
   justify-content: space-between;
}

.footer__main {
   flex: 0 0 300px;

}

.footer__logo {
   font-size: 30px;
   font-weight: 900;
   letter-spacing: 1.2px;
}


.footer__text {
   color: #8E8E8E;
   font-size: 16px;
   font-weight: 400;
   line-height: 30px;
   letter-spacing: 0.4px;
}

.footer__social {
   display: flex;
   column-gap: 10px;
   padding-top: 20px;
}

.footer__social li:hover {
   opacity: 0.7;
}


.menu-footer {
   display: flex;
   column-gap: 50px;
}

.menu-footer__title {
   font-weight: 700;
   padding-bottom: 10px;
}

.menu-footer__item li{
   padding: 5px 0;
}

.menu-footer__link {
   color: #fff;
   font-size: 12px;

}

.menu-footer__link:hover {
   opacity: 0.7;
}