.arrivals {
   margin: 80px 0;
}

.arrivals__title {
   display: inline-block;
   position: relative;
   font-size: 48px;
   font-weight: 900;
   line-height: 50px; /* 104.167% */
   letter-spacing: 2.88px;
   text-transform: uppercase;
}

.arrivals__title::after {
   position: absolute;
   content: '';
   right: 0;
   bottom: 10px;
   width: 190px;
   height: 32px;
   background-image: url('./../../img/icons/decor-yellow.svg');
   z-index: -1;
   transform: rotate(-5deg);
}

.arrivals__list {
   margin: 40px 0;
   display: flex;
   column-gap: 30px;
   justify-content: space-between;
}

.arrivals__item {
   overflow: hidden;
}
