

.download__content {
   display: flex;
   justify-content: center;
   align-items: center;
   column-gap: 160px;
}

.download__text {
   font-size: 40px;
   font-weight: 900;
   line-height: 1.2;
   letter-spacing: 2.88px;
}

.download__sub-text {
   margin: 40px 0;
   color: #7C7C7C;
   font-size: 18px;
   font-weight: 500;
}

.download__btn {
   display: flex;
   column-gap: 40px;
}

.download__link {
   transition: opacity 0.2s ease-out;
}

.download__link:hover {
   opacity: 0.7;
}
