.promo__content {
   display: flex;
   column-gap: 30px;

   padding: 68px 0 70px 80px;

   background-color: #F4F6F5;
   border-radius: 59px;
}

.promo__text {
   font-size: 66px;
   font-weight: 900;
   line-height: 100px;
}

.promo__text-bg {
   position: relative;
}

.promo__text-bg span {
   position: relative;

}

.promo__text-bg::before {
   content: '';
   position: absolute;
   width: 350px;
   height: 90px;
   left: -20px;
   background-color: #ebd96b;
   transform: rotate(-5deg);
   
}



.promo__link {
   margin-left: 20px;
   background-color: black;
   color: #fff;
   padding: 5px 25px;
   border: 1px solid #000;
   border-radius: 7px;
   font-size: 22px;
   font-weight: normal;
   transition: all 0.2s ease-in;
}

.promo__link:hover {
   background-color: #fff;
   color: black;
   border: 1px solid #000;
}

.promo__btn::before {
   content: '';
   width: 190px;
   height: 32px;
   display: inline-block;
   background-image: url('./../../img/icons/decor-yellow.svg');

}

.promo__sub-text {
   font-size: 18px;
   font-weight: normal;
}