.header {
   padding: 46px 0 56px;
}

.header__row {
   display: flex;
   justify-content: space-between;
   align-items: center;
   column-gap: 30px;
}

.header__logo-link {
   display: flex;
   align-items: center;
   column-gap: 15px;


   font-size: 40px;
   font-weight: 900;
   letter-spacing: 1.2px;
   text-transform: uppercase;
}

.header__nav ul {
   display: flex;
   align-items: center;
   column-gap: 35px;

   font-size: 16px;
   font-weight: 500;
   letter-spacing: 0.22px;
}

.header__nav-btn {
   background-color: black;
   color: #fff;
   padding: 10px 20px;
   border: 1px solid #000;
   border-radius: 7px;
   transition: all 0.2s ease-in;
}

.header__nav-btn:hover {
   background-color: #fff;
   color: black;
   border: 1px solid #000;
   border-radius: 7px;
}

.header__link {
   position: relative;
}

.header__link::before {
   content: "";
   position: absolute;
   width: 100%;
   height: 1px;
   background-color: #D4C17F;
   bottom: -4px;
   left: 0;
   transform-origin: right;
   transform: scaleX(0);
   transition: transform 0.3s ease-in-out;
}

.header__link:hover::before {
   transform-origin: left;
   transform: scaleX(1);

}