.payday {
   background-color: #ffed65;
}

.payday__body {
   display: flex;
   justify-content: center;
   align-items: center;
   column-gap: 60px;
}

.payday__img {
   min-width: 50%;
}

.payday__title {
   font-size: 80px;
   font-weight: 900;
   line-height: 1.31;
}

.payday__title-bg {
   position: relative;
}

.payday__title-bg span {
   position: relative;
}

.payday__title-bg::before {
   content: '';
   position: absolute;
   background-color: #fff;
   width: 420px;
   height: 100px;
   left: -20px;
   transform: rotate(-3deg);
}

.payday__txt {
   font-size: 22px;
   font-weight: 500;
   line-height: 1.11;
}

.payday__txt p {
   padding: 10px 0;
}

.payday__text-bold {
   font-weight: 700;
}

.payday__link {
   display: inline-block;
   margin-top: 20px;
   background-color: black;
   color: #fff;
   padding: 5px 35px;
   border: 1px solid #000;
   border-radius: 7px;
   font-size: 22px;
   transition: all 0.2s ease-in;
   font-weight: 700;
}

.payday__link:hover {
   background-color: #fff;
   color: black;
   border: 1px solid #000;
}