.card__img {
   max-height: 450px;
   border-radius: 10px;
}

.card__link {
   transition: all 0.3s ease-in;
}

.card__link:hover {
   opacity: 0.8;
   outline: 2px dashed #fff; /* Ширина, вид и цвет рамки */
   outline-offset: -10px; /* Выводим рамку внутри элемента */
}


.card__title {
   font-size: 26px;
   font-weight: 500;
   line-height: 50px; /* 156.25% */
   letter-spacing: -1.28px;
}

.card__text {
   font-size: 18px;
   font-weight: 500;
   line-height: 20px; /* 208.333% */
   letter-spacing: -0.96px;
   color: #7f7f7f;
}